.video {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.video__container {
  margin-top: 17px;
  margin-bottom: 38px;

  @media (min-width: @mobile-width-only) and (max-width: @tablet-width) {
    &::before {
      content: "";
      position: absolute;
      top: -1px;
      width: 320px;
      height: 1px;
      background: #e5e5e5;
    }
  }
}

.video__title {
  padding-bottom: 37px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    padding-bottom: 35px;
  }
}

.video__title,
.video__description {
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;

  @media (min-width: @tablet-width) {
    width: 580px;
  }
}

.video__image {
  @media (min-width: @tablet-width) {
    width: 648px;
    height: 500px;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
    height: 500px;
  }
}
